import React from "react"
import { Modal } from "react-responsive-modal"
import useScript from "../../hooks/external-scripts"

interface IProps {
	id: string
	form: string
	showModal: boolean
	onCloseModal(): void
}

const RdModal: React.FC<IProps> = (props: IProps) => {
	const { id, form, showModal, onCloseModal } = props

	useScript(
		"https://d335luupugsy2.cloudfront.net/js/rdstation-forms/stable/rdstation-forms.min.js",
		true
	)
	useScript(form, false)

	return (
		<Modal open={showModal} onClose={onCloseModal} center>
			<div role="main" id={id}></div>
		</Modal>
	)
}

export default RdModal
