import React, { useState } from "react"
import { translate } from "../../../../utils/translate"

export const Calculator = () => {
	const initialPrice = Number(translate("Audit.auditPrice"))
	const currency = translate("Audit.auditCurrency")

	const [audit, setaudit] = useState(1)
	const [company, setCompany] = useState(1)
	const [price, setPrice] = useState(
		`${currency}${initialPrice
			.toFixed(2)
			.replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,")}`
	)

	return (
		<div style={{ display: "flex", alignItems: "center", flexWrap: "wrap" }}>
			<div style={{ flex: 1, flexDirection: "column", textAlign: "center" }}>
				<div style={{ flex: 1 }}>
					<label htmlFor="Ncompany" style={{ color: "white" }}>
						Número de Auditorias
					</label>
				</div>
				<div style={{ flex: 1 }}>
					<input
						id="Ncompany"
						value={audit}
						style={{
							backgroundColor: "white",
							textAlign: "center",
							flex: 1,
							borderRadius: 10,
						}}
						onChange={event => {
							const newValue = Number(event.target.value)
							setaudit(newValue)

							const newPrice = (newValue * initialPrice) / company
							setPrice(
								`${currency}${newPrice
									.toFixed(2)
									.replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,")}`
							)
						}}
					/>
				</div>
			</div>
			<div style={{ flex: 1, flexDirection: "column", textAlign: "center" }}>
				<div style={{ flex: 1 }}>
					<label htmlFor="Naudit" style={{ color: "white" }}>
						Número de Empresas
					</label>
				</div>
				<div style={{ flex: 1 }}>
					<input
						id="Naudit"
						value={company}
						style={{
							backgroundColor: "white",
							textAlign: "center",
							borderRadius: 10,
						}}
						onChange={event => {
							let newValue = Number(event.target.value)
							if (newValue === 0) {
								setCompany(newValue)

								newValue = 1
								const newPrice = (audit * initialPrice) / newValue
								setPrice(
									`${currency}${newPrice
										.toFixed(2)
										.replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,")}`
								)
							} else {
								setCompany(newValue)

								const newPrice = (audit * initialPrice) / newValue
								setPrice(
									`${currency}${newPrice
										.toFixed(2)
										.replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,")}`
								)
							}
						}}
					/>
				</div>
			</div>
			<div style={{ flex: 1, flexDirection: "column", textAlign: "center" }}>
				<div style={{ flex: 1 }}>
					<label htmlFor="auditPrice" style={{ color: "white" }}>
						Valor no auditoria 360º
					</label>
				</div>
				<div style={{ flex: 1 }}>
					<input
						id="auditPrice"
						value={price}
						disabled={true}
						step="0.01"
						min="0.00"
						style={{
							backgroundColor: "white",
							textAlign: "center",
							borderRadius: 10,
						}}
					/>
				</div>
			</div>
		</div>
	)
}
