import React from "react"
import Layout from "../components/Layout"
import Audit from "../components/pages/Audit"
import SEO from "../components/utils/SEO"
import { translate } from "../components/utils/translate"
import { IBreadCrumbOption } from "../interfaces"

const SuplierQualification = () => {
	const breadCrumbList: IBreadCrumbOption[] = [
		{
			url: "/",
			name: "Home",
			isActive: true,
		},
		{
			url: "/solutions",
			name: translate("breadCrumb.solutions"),
			isActive: true,
		},
		{
			url: "/qualificacao-de-fornecedores",
			name: translate("Audit.title"),
			isActive: false,
		},
	]

	return (
		<Layout breadCrumbList={breadCrumbList}>
			<SEO
				title="Soluções para o seu negócio"
				description="A Kivalita Consulting tem as mais diversas soluções para o seu negócio. Encontre abaixo nossos serviços e clique para saber mais."
			></SEO>
			<Audit />
		</Layout>
	)
}

export default SuplierQualification
